	module.exports = {"data":{"content":{"content":"<p>正常情況下，我們會用命令<code>react-native run-android</code>來在自己的電話中測試app，但是這個方法需要連接電腦。</p>\n<p>我們可以利用以下命令：</p>\n<pre><code><pre><code class=\"hljs javascript\">react-native bundle --dev <span class=\"hljs-literal\">false</span> --platform android --entry-file index.android.js --bundle-output ./android/app/build/intermediates/assets/debug/index.android.bundle --assets-dest ./android/app/build/intermediates/res/merged/debug\n\ncd android\n./gradlew assembleDebug\n</code></pre></code></pre>\n<h2 id=參考資料>參考資料</h2>\n<ol>\n<li><a href=\"http://stackoverflow.com/questions/35283959/build-and-install-unsigned-apk-on-device-without-the-development-server\">Build and Install unsigned apk on device without the development server? - stackoverflow</a></li>\n</ol>\n","title":"【React Native】安裝 Android Apk 而無需連接伺服器的方法","createdDate":"2017-04-11T17:41+08:00","headerImgur":"IYhTVXU.png","id":"1HT1m5wiHaoe2ccoayqC2m","toc":["參考資料"],"tags":"React Native"},"latestPosts":{"totalCount":60,"edges":[{"node":{"title":"【網易考拉校招】部門總監面對面-直播答疑邀約（附網易內推碼B222E54）","url":"netease-kaola-live","createdDate":"2018-08-01T20:00+08:00"}},{"node":{"title":"Uncaught ReferenceError: regeneratorRuntime is not defined 解決方法","url":"regenerator-runtime-is-not-defined","createdDate":"2018-07-29T20:00+08:00"}},{"node":{"title":"關於作者","url":"about-calpa-liu","createdDate":"2018-05-01T20:00+08:00"}},{"node":{"title":"【方法】在直接複製貼上內容的時候，可能已經不知不覺注入了一段不可視的「空白」","url":"insert-zero-width-characters","createdDate":"2018-04-06T23:00+08:00"}},{"node":{"title":"知乎無限 live 數據流網站","url":"zhihu-unlimited-lives-website","createdDate":"2018-03-28T20:49+08:00"}},{"node":{"title":"Gatsbyjs async/await 更新報錯解決方法","url":"gatsybjs-async-await-upgrade-bug-solution","createdDate":"2018-03-21T20:00+08:00"}}]}},"pathContext":{"id":"1HT1m5wiHaoe2ccoayqC2m"}}

